import React, { Component } from 'react';
import * as d3 from "d3";
import moment from 'moment';

class Examchart extends Component {
    state = {
        change: false
    }
    componentDidUpdate() {

        // const { svg_id } = this.props
        // d3.select("#" + svg_id).remove()
        // this.drawChart()
    }

    componentDidMount() {
        this.drawChart();
    }
    drawChart() {
        function formattimetoInteger(time) {
            return new Date(time).getTime()
        }
        const { Time, id_key, svg_id, max, min, lineChart } = this.props
        console.log(Time)
        console.log(max)
        console.log(min)
        const Start_time = Time[0]
        const End_time = Time[1]
        const interval = (End_time - Start_time) / (24 * 60 * 60 * 1000)
        const viewwidth = window.screen.availWidth - 218 - 15

        var x = d3.scaleLinear().domain([Start_time, End_time]).range([0, 1000])
        var y = d3.scaleLinear().domain([0, 100]).range([100, 0])

        var svg = d3.select("#" + id_key)
            .append("svg")
            .style("height", 300)
            .style("width", 900)
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 10 + ")");

        svg.append('line')
            .attr('x1', x(Start_time))
            .attr('y1', y(0))
            .attr('x2', x(End_time))
            .attr('y2', y(0))
            .style('stroke', 'black')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        svg.append('line')
            .attr('x1', x(Start_time))
            .attr('y1', y(100))
            .attr('x2', x(End_time))
            .attr('y2', y(100))
            .style('stroke', 'black')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        console.log(lineChart)
        for (let index = 0; index < lineChart.length; index++) {
            const element = lineChart[index];
            const time = moment(element.date, "YYYY/MM/DD HH:mm:ss").valueOf()
            const data = Number(element.value)
            console.log(time, data)
            svg.append('circle')
                .attr('cx', x(time))
                .attr('cy', y(data))
                .attr('r', 3)
                .style('opacity', 0.5)
                .style('fill', "black")

        }
        console.log(interval)
        for (let index = 0; index < interval + 1; index++) {
            svg.append('line')
                .attr('x1', x(Start_time + (index * 24 * 60 * 60 * 1000)))
                .attr('y1', y(min))
                .attr('x2', x(Start_time + (index * 24 * 60 * 60 * 1000)))
                .attr('y2', y(max))
                .style('stroke', 'black')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);
        }
    }

    render() {
        const { id_key } = this.props
        return (
            <div id={id_key}></div>
        );
    }
}

export default Examchart;