import React, { Component } from 'react';
import { Icon, Upload, Table, Input, DatePicker, Button, Switch } from 'antd';
import { Link } from 'react-router-dom';
import * as ApiProvider from '../API/ApiProvider';


class Historyview extends Component {
    state = {
        MouseClick: 0,
        allPatientDisplayData: [],
        allPatientUnset: [],
        allPatientSet: [],
        selectedRowsArray: [],
        hospitalindex: '',
        selectedFile: null,
        fileList: [],
        uploading: false,
    }

    componentDidMount() {
        console.log("Historyview componentDidMount")
        ApiProvider.getAllPatient()
            .then(result => {
                console.log("getAllPatient OK " + JSON.stringify(result));
                const setedUser = result.data.filter(item => item.transouttime !== "unset");
                const unsetUser = result.data.filter(item => item.transouttime === "unset");
                this.setState({
                    allPatientDisplayData: setedUser,
                    allPatientUnset: unsetUser,
                    allPatientSet: setedUser
                });
            })
            .catch(err => {
                console.log("getAllPatient NG " + JSON.stringify(err));
            });
    }

    MouseClickTable(record) {
        //console.log(this.state.MouseClick)
        this.setState({
            MouseClick: record
        })
    }

    EditStyle(HISID, CASEID, TRANSINID, key) {
        if (key === this.state.MouseClick) {
            return (
                <Link to={{ pathname: "/nicu/patient/" + HISID, state: {"source": [], "HISID": HISID, "CASEID": CASEID, "BEDNUMBER": 0, "TYPE": false, "TRANSINID": TRANSINID } }} style={{ height: "40px", width: "100px", borderRadius: "4px" }}>詳細</Link>
            );
        }
        else return null
    }

    genderswitch(key) {
        if (key === 1) {
            return '男'
        }
        else {
            return '女'
        }
    }

    bornweeks(birthday) {
        //console.log(Math.floor(birthday / 7))
        //console.log(birthday % 7)
        const weeks = Math.floor(birthday / 7)
        const days = birthday % 7
        if (days === 0) {
            return weeks + '週歲'
        }
        else return weeks + '週' + days + '天'
    }

    exportClick() {
        this.state.selectedRowsArray.map((data, index) => {
            ApiProvider.exportJson(data.hospitalindex)
                .then(result => {
                    ApiProvider.downloadFile(result.data.filename)
                        .then(resultD => {
                            const url = window.URL.createObjectURL(new Blob([JSON.stringify(resultD.data)]));
                            const link = document.createElement('a');
                            link.href = url;
                            link.setAttribute('download', result.data.filename);
                            document.body.appendChild(link);
                            link.click();
                        }).catch(err => { })
                })
                .catch(err => { })
        })
    }

    handleUpload = () => {
        const { fileList } = this.state
        const formData = new FormData()
        fileList.forEach(file => {
            formData.append('file', file)
        })

        this.setState({
            uploading: true,
        })

        ApiProvider.uploadFile(formData)
            .then(res => {
                ApiProvider.importJson(res.data)
                    .then(result => {
                        ApiProvider.getAllPatient()
                            .then(result => {
                                const setedUser = result.data.filter(item => item.transouttime !== "unset");
                                const unsetUser = result.data.filter(item => item.transouttime === "unset");
                                this.setState({
                                    allPatientDisplayData: setedUser,
                                    allPatientUnset: unsetUser,
                                    allPatientSet: setedUser
                                });
                            })
                            .catch(err => {
                                console.log("getAllPatient NG " + JSON.stringify(err));
                            });
                    })
                    .catch(err => { })
            })
            .catch(err => { })
    }

    deleteHistoryUser() {
        this.state.selectedRowsArray.map((data, index) => {
            ApiProvider.deletePatient(data.hospitalindex, data.hospitalnumber, data.id)
                .then(res => {
                    ApiProvider.getAllPatient()
                        .then(result => {
                            const setedUser = result.data.filter(item => item.transouttime !== "unset");
                            const unsetUser = result.data.filter(item => item.transouttime === "unset");
                            this.setState({
                                allPatientDisplayData: setedUser,
                                allPatientUnset: unsetUser,
                                allPatientSet: setedUser
                            });
                        })
                        .catch(err => {
                            console.log("getAllPatient NG " + JSON.stringify(err));
                        });
                })
                .catch(err => { })
        })
    }

    Searchbar() {
        const InputGroup = Input.Group;
        return (
            <div style={{ display: 'flex', alignItems: 'center' }}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div style={{ fontSize: '16px' }}>
                        個案姓名:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Input placeholder="輸入個案姓名"></Input>
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        轉出時間:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <InputGroup compact>
                            <DatePicker placeholder="選擇轉出時間" />
                        </InputGroup>
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        病歷號碼:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Input placeholder="輸入病歷號碼"></Input>
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        主治醫生:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Input placeholder="選擇主治醫師"></Input>
                    </div>
                </div>
            </div>
        );
    }

    handleDisplaySwitchChange(checked, event) {
        this.setState({
            allPatientDisplayData: checked ? this.state.allPatientSet : this.state.allPatientUnset
        })
    }


    render() {
        // rowSelection object indicates the need for row selection
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
                //console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
                this.setState({ selectedRowsArray: selectedRows })
            },
            getCheckboxProps: record => ({
                disabled: record.name === 'Disabled User', // Column configuration not to be checked
                name: record.name,
            }),
        };
        const columns = [
            {
                title: '床號',
                dataIndex: 'bednumber',
                key: 'bednumber',
                width: "5%"
            },
            {
                title: '病人ID',
                dataIndex: 'id',
                key: 'id',
                width: "10%"
            },
            {
                title: '個案名稱',
                dataIndex: 'name',
                key: 'name',
                width: "10%"
            },
            {
                title: '性別',
                dataIndex: 'gender',
                key: 'gender',
                width: "5%"
            },
            {
                title: '體重',
                dataIndex: 'weight',
                key: 'weight',
                width: "5%"
            },
            {
                title: '出生週期',
                dataIndex: 'birweek',
                key: 'birweek',
                width: "10%"
            },
            {
                title: '轉入時年齡',
                dataIndex: 'transage',
                key: 'transage',
                width: "10%"
            },
            {
                title: '住院編號',
                dataIndex: 'hospitalnumber',
                key: 'hospitalnumber',
                width: "10%"
            },
            {
                title: '住院序號',
                dataIndex: 'hospitalindex',
                key: 'hospitalindex',
                width: "10%"
            },
            {
                title: '轉出ICU時間',
                dataIndex: 'transicutime',
                key: 'transicutime',
                width: "10%"
            },
            {
                title: '主治醫師',
                dataIndex: 'maindoc',
                key: 'maindoc',
                width: "10%"
            },
            {
                dataIndex: 'clickstate',
                key: 'clickstate',
                width: "5%"
            }
        ];

        const data = this.state.allPatientDisplayData.map((item, index) => {
            return {
                key: index,
                bednumber: 'B' + index,
                id: item.hisid,
                name: item.pnamec,
                gender: this.genderswitch(item.psex),
                weight: 2000,
                birweek: this.bornweeks(27),
                transage: this.bornweeks(14),
                hospitalnumber: item.caseid,
                hospitalindex: item.transinid,
                transicutime: item.transouttime,
                maindoc: "陳ＯＯ",
                clickstate: this.EditStyle(item.hisid, item.caseid, item.transinid, index)
            }
        });

        const { uploading, fileList } = this.state;
        const props = {
            onRemove: file => {
                this.setState(state => {
                    const index = state.fileList.indexOf(file);
                    const newFileList = state.fileList.slice();
                    newFileList.splice(index, 1);
                    return {
                        fileList: newFileList,
                    };
                });
            },
            beforeUpload: file => {
                this.setState(state => ({
                    fileList: [...state.fileList, file],
                }));
                return false;
            },
            fileList,
            showUploadList: false
        };
        /*
        const data = [
            {
                key: 1,
                bednumber: 'B01',
                id: 'G123456789',
                name: 'John Brown',
                gender: this.genderswitch('Male'),
                weight: 2000,
                birweek: this.bornweeks(27),
                transage: this.bornweeks(14),
                hospitalnumber: 12345,
                hospitalindex: 12345,
                transicutime: '2019/8/8',
                maindoc: "陳ＯＯ",
                clickstate:this.EditStyle(1)
            },
            {
                key: 2,
                bednumber: 'B02',
                id: 'G123456789',
                name: 'John Brown',
                gender: this.genderswitch('Male'),
                weight: 2000,
                birweek: this.bornweeks(47),
                transage: this.bornweeks(14),
                hospitalnumber: 12345,
                hospitalindex: 12345,
                transicutime: '2019/8/8',
                maindoc: "陳ＯＯ",
                clickstate: this.EditStyle(2)
            },
            {
                key: 3,
                bednumber: 'B03',
                id: 'G123456789',
                name: 'John Brown',
                gender: this.genderswitch('FeMale'),
                weight: 2000,
                birweek: this.bornweeks(14),
                transage: this.bornweeks(14),
                hospitalnumber: 12345,
                hospitalindex: 12345,
                transicutime: '2019/8/8',
                maindoc: "陳ＯＯ",
                clickstate: this.EditStyle(3)
            },
        ];*/
        const handleDisplaySwitchChange = this.handleDisplaySwitchChange.bind(this);

        return (
            <div style={{ paddingLeft: "24px", paddingRight: "24px" }}>
                <div style={{ fontSize: "22px", height: '50px', lineHeight: "50px" }}>
                    歷史病歷管理
                </div>
                <Switch checkedChildren="Set" unCheckedChildren="Unset" defaultChecked onChange={handleDisplaySwitchChange} />
                <div style={{ display: "grid", gridTemplateColumns: "3fr 1fr", height: '50px', gridColumnGap: "10px" }}>
                    {this.Searchbar()}
                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gridGap: "10px" }}>
                        <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                            <Upload {...props}>
                                <Button>
                                    <Icon type="upload" /> 選擇檔案
                                </Button>
                            </Upload>
                            <Button
                                onClick={this.handleUpload.bind(this)}
                                disabled={fileList.length === 0}
                                style={{ width: "100%" }}
                            >
                                匯入
                            </Button>
                        </div>
                        <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                            <Button onClick={this.exportClick.bind(this)} style={{ width: "100%" }}>匯出</Button>
                        </div>
                        <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                            <Button onClick={this.deleteHistoryUser.bind(this)} style={{ width: "100%" }}>刪除</Button>
                        </div>
                    </div>
                </div>
                <div>
                    <Table rowSelection={rowSelection} columns={columns} dataSource={data} onRow={record => {
                        return {
                            onMouseEnter: event => { this.MouseClickTable(record.key) }, // 鼠标移入行
                            onMouseLeave: event => { this.MouseClickTable(0) } // 鼠标移入行
                        };
                    }} />
                </div>
            </div>
        );
    }
}

export default Historyview;