import React, { Component } from 'react';
import './Nonlabviewtable.scss'
import { Nonlablist } from '../../Data/Wardviewdata-jack'
import { yyyymmddformat } from '../../Function/Timeformat'
import NonlabviewtableCell from './NonlabviewtableCell'
import { Modal } from 'antd';
import InsReportModal from '../ExamReportModal'

class Nonlabviewtable extends Component {
    state = {
        visible: false
    }

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };

    render() {
        function listforstatus(ordlist, ressect, pickerdate) {
            var status_report = []
            console.log(ordlist, ressect, pickerdate)
            const datadate = new Date(pickerdate).getTime()
            const dateselect = new Date(pickerdate).getTime() - (3 * 24 * 60 * 60 * 1000)
            if (Array.isArray(ordlist) && Array.isArray(ressect)) {
                for (let index = 0; index < ordlist.length; index++) {
                    const element = ordlist[index];
                    if (new Date(element.ORDATE).getTime() > dateselect && new Date(element.ORDATE).getTime() < datadate) {
                        const status = ressect.find(function (value, index, array) {
                            return value.ORDCLNM === element.ORDCLNM
                        })
                        if (status) {
                            status_report.push({
                                "status": true,
                                "element": element
                            })
                        }
                        else {
                            status_report.push({
                                "status": false,
                                "element": element
                            })

                        }
                    }
                }
            }
            return status_report
        }
        const { PickerDate, ORDLIST, RESSECT } = this.props
        //Picker Date 前三天
        const list = listforstatus(ORDLIST, RESSECT, PickerDate)

        const visible = this.state.visible;
        const showModal = this.showModal
        const handleOk = this.handleOk;
        const handleCancel = this.handleCancel;
        const nonlabcell = list.map(function (value, index, array) {
            console.log(value.element)
            const come = value.element.ORSPCN1;
            const time = value.element.ORDATE;
            const name = value.element.ORPROCED;
            const status = value.status;
            return (
                <div>
                    <NonlabviewtableCell come={come} time={time} name={name} statis={status} hisId={value.element.ORHISTNO} caseId={value.element.ORDSEQCN} month={value.element.ORMM} seq={value.element.ORDSEQNO} showModal={showModal}></NonlabviewtableCell>
                    <Modal
                        visible={visible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                        width={1000}
                        footer={null}>
                        <InsReportModal></InsReportModal>
                    </Modal>
                </div>
            );
        })

        return (
            <div>
                <div style={{ height: "50px", border: "#E2EDF3 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr" }}>
                    <t3 className='nonlabtebletitlecell'>檢驗日期</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目名稱</t3>
                    <t3 className='nonlabtebletitlecell'>檢體來源</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目狀態</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗結果</t3>
                </div>
                <div className='nonlabviewtable'>
                    {nonlabcell}
                </div>
            </div>
        );
    }
}

export default Nonlabviewtable
