import React, { Component } from 'react';
import Notecell from './Notecell'
import editlogo from '../Image/svg/Edit.svg'
import { Input, Button } from 'antd';
import * as ApiProvider from '../API/ApiProvider';
class Note extends Component {
    state = {
        powerLogin: 0,
        EditMode: false,
        inputContent: '',
        noteData: []
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }
    onInputChange(event) {
        this.setState({ inputContent: event.target.value })
    }
    onChangeEditMode() {
        this.setState(
            {
                EditMode: !this.state.EditMode
            }
        )
    }
    queryOnePatientNotice() {
        ApiProvider.getOnePatientNotice(this.props.caseinfo.TRANSINID, this.props.caseinfo.CASEID)
            .then(data => {
                console.log(" OK " + JSON.stringify(data))
                this.setState({ noteData: data.data })
            }).catch(err => { })
    }
    onClickEnter() {
        const timeNowStr = new Date().toISOString()
        ApiProvider.createNotice(this.props.caseinfo.CASEID, this.state.inputContent, timeNowStr, this.props.caseinfo.TRANSINID)
            .then(data => {
                this.queryOnePatientNotice()
            })
            .catch(err => {
                console.log(" Err " + JSON.stringify(err))
            })
        this.setState(
            {
                EditMode: false,
                inputContent: undefined
            }
        )
    }
    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }

    render() {
        const notelist = this.props.data.map((info, index) => {
            const infoObj = {
                updatedictor: "林醫師",
                updatetime: "2019/12/12",
                data: info.context
            }
            return <Notecell key={index} info={infoObj}></Notecell>
        })
        return (
            <div style={{ marginTop: "10px", borderRadius: "4px" }}>
                <div style={{ height: "50px", backgroundColor: "#cccccc", padding: "0px 5px", display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <t2>{this.props.title}</t2>
                    <div onClick={() => this.onChangeEditMode()} style={{ display: this.state.powerLogin > 0 ? "flex" : "none" }}>
                        <img src={editlogo} alt="editlogo" />
                    </div>
                </div>
                <div style={{ height: "300px" }}>
                    <div style={{ maxHeight: "300px", overflow: "auto" }}>
                        {notelist}
                    </div>
                    <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                        <div style={{ padding: "5px 10px", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <Input placeholder="輸入當日內容" value={this.state.inputContent} onChange={this.onInputChange.bind(this)}></Input>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                                <Button onClick={() => this.onClickCancel()}>取消</Button>
                                <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Note