import React, { Component } from 'react';
import detaillogo from '../Image/svg/details.svg'
import editlogo from '../Image/svg/Edit.svg'
import { Tooltip, Input, Button, Select } from 'antd';
import './Schedule_event.scss'
import { yyyymmddformat, hhmmssformat } from '../Function/Timeformat'
import * as ApiProvider from '../API/ApiProvider';
//資料
// import { eventdata } from '../jsonResponse'
import { eventData } from '../Data/Wardviewdata-jack'

class Schedule_event_list extends Component {
    state = {
        detailstate: "none",
        pressitemstate: "all",
        items_interval: 0,
        scheduleeventdata: this.props.data,
        powerLogin: 0,
        EditMode: false,
        inputContent: '',
        selSchedule: undefined
    }

    componentDidMount() {
        this.onInputChange = this.onInputChange.bind(this);

        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }

    selSchedule(value) {
        this.setState({ selSchedule: value })
    }
    onInputChange(event) {
        this.setState({ inputContent: event.target.value })
    }
    onChangeEditMode() {
        this.setState(
            {
                EditMode: !this.state.EditMode
            }
        )
    }
    onClickEnter() {
        const timeNowStr = new Date().toISOString()
        ApiProvider.createPatientSchedule(this.props.caseinfo.PLOCSTART.HBEDNO, this.state.inputContent, timeNowStr, this.props.caseinfo.TRANSINID, this.state.selSchedule)
            .then(data => {
                console.log(" OK " + JSON.stringify(data))
            })
            .catch(err => {
                console.log(" Err " + JSON.stringify(err))
            })
        this.setState(
            {
                EditMode: false,
                inputContent: undefined
            }
        )
    }
    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }

    timechange(timestamp) {
        const changetime = new Date(timestamp).getHours() + ":" + new Date(timestamp).getMinutes()
        return changetime
    }

    pressbtn(pressitem) {
        this.setState(
            {
                pressitemstate: pressitem
            }
        );
    }
    listevent() {
        // var eventdatalist = eventdata
        const scheduleData = this.props.data;
        console.log(scheduleData)
        const eventarray = (scheduleData === undefined) ? [] : scheduleData.scheduleList;
        const eventstate = this.state.pressitemstate

        function itemchange(changeitem) {
            switch (changeitem) {
                case "事件":
                    return "事"
                case "排程":
                    return "排"
                default:
                    return ""
            }
        }

        function checkbackground(string) {
            switch (string) {
                case "排程":
                    return "rgba(124,179,5,1)"
                case "事件":
                    return "rgba(46,100,164,1)"
                default:
                    return "rgba(124,179,5,1)"
            }
        }
        var listarray = []
        for (let index = 0; index < eventarray.length; index++) {
            const info = eventarray[index];
            const text = <div>{info.writter}醫師於{yyyymmddformat(info.timestamp)} {hhmmssformat(info.timestamp)}更新</div>
            if (eventstate === "all") {
                listarray.push(
                    <div key={index} className='CellLayout'>
                        <div className='Context'>
                            <div style={{ height: '30px', width: "30px", borderRadius: "4px", backgroundColor: checkbackground(info.type), display: "flex", justifyContent: "center", alignItems: "center" }}>
                                <t2 style={{ color: 'white' }}>{itemchange(info.type)}</t2>
                            </div>
                            <div style={{ fontSize: "16px" }}>床{info.bednumber}/{info.name} {hhmmssformat(info.timestamp)} {info.content}</div>
                        </div>
                        <Tooltip placement="right" title={text}>
                            <div>
                                <img src={detaillogo} alt='detaillogo'></img>
                            </div>
                        </Tooltip>
                    </div>
                )
            }
            else {
                if (eventstate === info.type) {
                    listarray.push(
                        <div key={index} className='CellLayout'>
                            <div className='Context'>
                                <div style={{ height: '30px', width: "30px", borderRadius: "4px", backgroundColor: checkbackground(info.type), display: "flex", justifyContent: "center", alignItems: "center" }}>
                                    <t2 style={{ color: 'white' }}>{itemchange(info.type)}</t2>
                                </div>
                                <div style={{ fontSize: "16px" }}>床{info.bednumber}/{info.name} {hhmmssformat(info.timestamp)} {info.content}</div>
                            </div>
                            <Tooltip placement="right" title={text}>
                                <div>
                                    <img src={detaillogo} alt='detaillogo'></img>
                                </div>
                            </Tooltip>
                        </div>
                    )
                }
            }
        }
        if (listarray.length === 0) return (<div>本日無排程及事件</div>)
        return listarray
    }

    // eventdata() {
    //     //eventdata 假資料
    //     var eventdatalist = eventdata
    //     var Eventstate = this.state.pressitemstate
    //     return this.listevent(eventdatalist, Eventstate)
    // }

    //測驗項目功能_區間按鈕樣式
    SwitchTestItemInterval(testiteminterval) {
        const stylestate = this.state.pressitemstate
        if (testiteminterval === stylestate) {
            return 'buttonselect'
        }
        else return 'buttonUnselect'
    }
    //測驗項目功能_區間按鈕選擇
    Onchangetimeinterval(changestate) {
        this.setState(
            {
                items_interval: changestate
            }
        )
    }
    render() {
        const { title } = this.props
        const { Option } = Select
        return (
            <div className='ScheduleEventLayout'>
                <div className='ScheduleEventTitle'>
                    <t2>{title}</t2>
                    <div onClick={() => this.onChangeEditMode()} style={{ display: this.state.powerLogin > 0 ? "flex" : "none" }}>
                        <img src={editlogo} alt="editlogo" />
                    </div>
                </div>
                <div className='ScheduleEventContext'>
                    <div className='ContextControl'>
                        <div className='buttonGroup'>
                            <div className={this.SwitchTestItemInterval("all")} onClick={() => this.pressbtn("all")}>
                                <t2 className='titlecolor'>全部</t2>
                            </div>
                            <div className={this.SwitchTestItemInterval("排程")} onClick={() => this.pressbtn("排程")}>
                                <t2 className='titlecolor'>排程</t2>

                            </div>
                            <div className={this.SwitchTestItemInterval("事件")} onClick={() => this.pressbtn("事件")}>
                                <t2 className='titlecolor'>事件</t2>
                            </div>
                        </div>
                    </div>
                    <div className='ContextList'>
                        {this.listevent()}
                    </div>
                </div>
                <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                    <div style={{ padding: "5px 10px", display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <Select placeholder='選擇' style={{ width: 150 }} onChange={this.selSchedule.bind(this)}>
                            <Option value='排程'>排程</Option>
                            <Option value='事件'>事件</Option>
                        </Select>
                        <Input placeholder="輸入當日內容" value={this.state.inputContent} onChange={this.onInputChange}></Input>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                            <Button onClick={() => this.onClickCancel()}>取消</Button>
                            <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Schedule_event_list